.search {
    width: 100%;
    position: relative;
    display: flex;
  }
  .searchTerm {
    width: 30%;
    border: 3px solid #023e8a;
    border-right: none;
    padding: 5px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #9DBFAF;
  }
  
  .searchTerm:focus{
    color: #023e8a;
  }
  
  .btn-search {
    width: 15%;
    height: 36px;
    border: 1px solid #023e8a;
    background: #023e8a;
    text-align: center;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 20px;
  }
  
  .wrap{
    width: 50%;
    padding-top:2%;
    left: 50%; 
  }